
import React, { useState, useEffect, useRef } from 'react';
import SwitchType from "./AppSettingComponents/SwitchType";
import SelectType from "./AppSettingComponents/SelectType";
import { Grid, useMediaQuery } from "@mui/material";
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles((theme) => ({

    splitLine: {
        width: "calc(100% - 40px)",
        height: "1px",
        marginLeft: "20px",
        marginRight: "20px",
        backgroundColor: theme.palette.text.secondary,
    },
    borderCard: {
        margin: "0 auto",
        marginTop: 40,
        marginBottom: 40,
        width: 754,
        borderRadius: 20,
        color: theme.palette.background.mainCard,
        boxShadow: theme.palette.page.shadow,
        overflow: "hidden",
        // padding: "0px",
    },
    matches_borderCard: {
        margin: "0 auto",
        marginBottom: 40,
        paddingBottom: "20px",
        color: theme.palette.background.mainCard,
        overflow: "hidden",
        // padding: "10 10px 0 10px",
    },
}));

export default function AppSetting(props) {
    const classes = useStyles();
    const matches = useMediaQuery('(min-width:800px)');

    return (
        <div className={matches ? classes.borderCard : classes.matches_borderCard}   >
            <Grid
                container
                direction="column"
                justifyContent="flex-start"
                alignItems="flex-start"
                sx={{
                    with: "100%",
                }}
            >
                <Grid item sx={12} sx={{ width: "100%" }} >
                    <SwitchType
                        name={""}
                        defaultValue={false}
                        help={(value) => value ? "" : ""}
                    />
                </Grid>

                <div className={classes.splitLine} />
                
                <Grid item sx={12} sx={{ width: "100%" }}>
                    <SwitchType
                        name={""}
                        defaultValue={false}
                        help={(value) => value ? "" : ""}
                    />
                </Grid>

                <div className={classes.splitLine} />

                <Grid item sx={12} sx={{ width: "100%" }}>
                    <SelectType
                        name={""}
                        defaultValue={""}
                        values={["","",""]}
                        help={(value) => `${value}`}
                    />
                </Grid>

                <div className={classes.splitLine} />

                <Grid item sx={12} sx={{ width: "100%" }}>
                    <SelectType
                        name={""}
                        defaultValue={7}
                        values={[0, 3, 5, 7, 11, 13, 17]}
                        help={(value) => `${value}`}
                    />
                </Grid>

                <div className={classes.splitLine} />

                <Grid item sx={12} sx={{ width: "100%" }}>
                    <SwitchType
                        name={""}
                        defaultValue={false}
                        help={(value) => value ? "," : ""}
                    />
                </Grid>
            </Grid>
        </div>
    )

}
